@use '../../../abstract/' as *;

.activities {
    width: 100%;
    padding: $padding-in-section;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: $gap-in-section;

    &__main {
        width: 100%;
        position: relative;
        display: flex;
        justify-content: center;
    }

    &__footer {
        margin-top: $margin-section-footer;

        a {
            display: flex;
            justify-content: center;
            align-items: center;
        }
    }


}